import classNames from 'classnames';
import styles from '../icons-react.module.scss';
import { settings } from 'carbon-components';
const { prefix } = settings;
interface UtilProps {
  size: string;
  fill: string;
  className?: string;
}

interface SizeUtilProps {
  value: string;
}

export const PropsToClassName = (props: UtilProps) => {
  const { size, fill, className } = props;
  const sizeClassName = styles[`${prefix}--svg__size--${size}`];
  const fillClassName = fill ? styles[`${prefix}--svg__fill--${fill}`] : '';
  return classNames(className, sizeClassName, fillClassName);
};

export const SizeToClassName = (props: SizeUtilProps) => {
  const { value } = props;
  return styles[`${prefix}--svg__size--${value}`];
};

export default PropsToClassName;
